@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  background-color: #ECF9FF;

}

nav {
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.nav--header {
  height: 80px;
  display: flex;
  justify-content: center;
  padding: 10px 0 10px 0;
}

.nav--logo {
  max-width: 100%;
}

.card {
  display: flex;
  position: relative;
  margin: 100px 0 0 50px;
}

.card--img-container {
  width: 125px;
  height: 168px;
  margin-right: 20px;
  flex: 0 0 auto;
}

.card--img-container__img {
  max-width: 100%;
  border-radius: 5px;
}

.card--content__location-detail {
  display: flex;
  align-items: center;
}

.card--location__icon {
  margin-right: 5px;
  font-size: 9.55px;
  color: red;
}

.card--location__country {
  text-transform: uppercase;
  font-size: 10.24px;
  font-weight: 400;
  letter-spacing: 0.17em;
  margin-right: 12px;
}

.card--location__map {
  font-weight: 400;
  font-size: 10.24px;
  color: #918e9b;
}

.card--content__heading {
  font-weight: 700;
  font-size: 25px;
  color: #2b283a;
  margin-top: 0;
}

.card--content__date {
  font-weight: 700;
  font-size: 10.24px;
  color: #2b283a;
}

.card--content__desc {
  font-weight: 400;
  font-size: 0.8rem;
  color: #2b283a;
  margin: 0 50px 38px 0;
}

hr {
  border: 1px solid #f5f5f5;
  margin: 5rem 3rem 0 3rem;
  /* background-color: black; */
}

